.services_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap:3rem;
}

.service{
    border: 1px solid var(--color-primary);
    border-radius: 2rem;
    height: fit-content;
    transition: var(--transition);
}

.service:hover{
    border: transparent;
    transition: var(--transition);
    background: var(--color-dark);
    color: #fff;
    cursor: default;
}

.service_head{
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}

.service_head h3{
    font-size: 1.5rem;
    text-align: center;
}

.service_list{
    padding: 2rem;
}

.service_list li{
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service_icon{
    margin-top: 3px;
}

.service_list p{
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px){
    .services_container{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
    .service{
        height: auto;

    }
}

@media screen and (max-width: 600px){
    .services_container{
        grid-template-columns: 1fr;
        gap:1.5rem;
    }
}
